.product{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin:10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    background-color: white;
    z-index: 1;

}

.product > img {
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}

.product__rating{
    display: flex;

}

.product__info{
    height: 100px;
    margin-bottom: 15px;
}

.product > button{
    background-color: #f0c14b;
    border: solid 1px;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
    cursor: pointer;
    width: 160px;
    height:40px;
    box-shadow: 1px 1px 1px 1px rgb(229, 118, 88);
}

.product__price{
    margin-top: 5px;
}