.home{
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;
}

.home__row{
    display: flex;
    z-index: 1;
    /* position: relative; */
    margin-left: 5px;
    margin-right: 5px;
    /* margin-top: 120px; */
}

.home__image{
    width: 100%;
    z-index: -1;
    margin-bottom: -150px;
    mask-image: linear-gradient(to bottom,rgba(0,0,0,1),rgba(0,0,0,0));
}
h2{
    margin-top: 16px;
    margin-left: 16px;
}